import React, { Component } from 'react';

import listeCommandes from './commandes';

class Qr extends Component{

    constructor(props)
    {
        super(props);
 
    
    }

    get = this.props.location.search;

    extractParamsUrl(chaineGET){
        chaineGET = chaineGET.split('&');
        var result = {};
    
        chaineGET.forEach(function(el){
            var param = el.split('=');
            param[0] = param[0].replace('?', '');
            result[param[0]] = param[1];
        });

        return result;
    }

    qr =() =>{
        var num = this.extractParamsUrl(this.get).num
        listeCommandes.numTable = parseInt(num)
        this.props.history.push('/');
    }
    render() {
        this.qr()
        return ( 
            <di></di>
        );
    }
}

export default Qr;