import firebase from "./firebase";
import React, { Component } from 'react';
const db = firebase.ref("/commandes");

 class DataCommande extends Component {

  getAll() {
    return db;
  }

  create(comm) {
    return db.push(comm);
  }

  update(key, value) {
    return db.child(key).update(value);
  }

  delete(key) {
    return db.child(key).remove();
  }

  deleteAll() {
    return db.remove();
  }

  
   render() {
    
     return (
       <div>
         {
           this.props.entries.map(item =>(
             <ul className="list-group">
               <li className="list-group-item list-group-item-action text-white bg-info">
                 <div className="container">
                   <div className= 'row'>
                        <div className='col-8'>
                            commande table : {item.numTable}
                        </div>
                        <div className='col-4'>
                            Prix : {item.prixCom}
                        </div>
                   </div>
                    
                 </div>
                 
                 </li>
               <li className="list-group-item bg-dark text-white">
                            <div className="container">
                                <div className="row">
                                    <div className='col-6 border-right'>
                                        Nom
                                    </div>
                                    <div className='col-3 border-right'>
                                        Qte
                                    </div>
                                    <div className='col-3'>
                                        Prix
                                    </div>
                                    
                                </div>
                            </div>
                  </li>
                 {
                   item.content.map(el =>(
                    <li className="list-group-item">
                    <div className="container border-bottom">
                        <div className="row">
                            <div className='col-6 border-right'>
                                {el.title}
                            </div>
                            <div className='col-3 border-right'>
                                {el.quantite}
                            </div>
                            <div className='col-3'>
                                {el.price}
                            </div> 
                        </div>
                    </div>
                    </li>
                   ))
                 }
             </ul>
           ))
         }
         
       </div>
     );
   }
 }

export default DataCommande;