import React, { Component } from 'react';
import listeCommandes from './commandes';
import Confirmation from './confirmation';
import EntrezNum from './entrezNum';
import {FaTrash} from 'react-icons/fa';
import {Button} from 'react-bootstrap';
import * as firebase from 'firebase';
import "firebase/database";

import 'bootstrap/dist/css/bootstrap.min.css';
import config from './config';


class Commandes extends Component {
    constructor(props){
        super()
        this.state = {
            numTab : listeCommandes.numTable,
            commande: listeCommandes.content,
            toastShow: false,
            numShow : false,
        }
        if (!firebase.apps.length) {
            firebase.initializeApp(config);
        }
    }
    

    
    handleNum = () =>{ 
        this.setState({
        numShow: !this.state.numShow,
        numTab : listeCommandes.numTable
        })
    }

    actualise = () =>{
        this.setState({toastShow:false})
        window.location.reload();
    }

    handleConfirm = (data) =>{
        
        if(this.state.numTab == 0){
            this.setState({numShow:true})
            
        }
        else{
            const ref = firebase.database().ref("/commandes")
            ref.push(data);
            this.setState({
                toastShow:true, 
            })
            

        }
        
        
    }

    

    handleTrash = (id) => {
        var array = this.state.commande;
        array.splice(id,1)

        this.setState( { commande: array });
        this.props.handleClick(parseInt(-1))
    }
    render() {
        if (this.state.commande.length === 0){
            return <div className="alert alert-primary" role="alert">
                   Aucun plat selectionée
                </div>
        
        }
        return (
            <div className="mt-2">
                <ul class="list-group">
                    <li className="list-group-item bg-dark text-white">
                            <div className="container">
                                <div className="row">
                                    <div className='col-4'>
                                        Nom
                                    </div>
                                    <div className='col-3'>
                                        Qte
                                    </div>
                                    <div className='col-3'>
                                        Prix
                                    </div>
                                    <div className='col-2'>
                                        Actions
                                    </div>
                                </div>
                            </div>
                    </li>
                    {this.state.commande.map((commande,index)=>(
                        
                        <li className="list-group-item">
                            <div className="container">
                                <div className="row">
                                    <div className='col-4'>
                                        {commande.title}
                                    </div>
                                    <div className='col-3'>
                                        {commande.quantite}
                                    </div>
                                    <div className='col-3'>
                                        {commande.price}
                                    </div>
                                    <div className='col-2'>
                                        <FaTrash  onClick = {()=> this.handleTrash(index)}/>
                                    </div>
                                    
                                </div>
                            </div>
                        </li>
                    ))}
                </ul>
                <div className = "d-flex justify-content-center pt-2 pb-2 bg-dark shadow">
                <Button  variant = "success" onClick={() => this.handleConfirm(listeCommandes)}>Confirmation</Button>
                </div>
                
                <Confirmation
                    show = {this.state.toastShow}
                    onHide={() => this.actualise()}
                />

            <EntrezNum 
                show={this.state.numShow}
                onHide={() => this.setState({numShow:false})} 
                handleNum = {this.handleNum}
            />
            </div>
        );
    }

}

export default Commandes;